        renderLineChart(newValue) {
            let chartData = null;
            if (newValue === '') {
                chartData = this.guangdongData;
                chartData.datasets[0].label = this.$t('');
            }
            if (newValue === '') {
                chartData = this.anhuiData;
                chartData.datasets[0].label = this.$t('');
            }
