                    itemStyle: {
                        color: '#fff',
                        areaColor: {
                            type: 'linear', // 
                            x: 0,
                            y: 0,
                            x2: 0.5,
                            y2: 1,
                            colorStops: [
                                {
                                    offset: 0,
                                    color: '#5eb139' // 
                                },
