    "":"How to evaluate tea",
    "":"Tea appraisal is a traditional Chinese tea culture activity, usually hosted by tea masters or tea lovers. During the tea evaluation process, participants will taste different types of tea and evaluate its appearance, aroma, taste, soup color and other aspects.",
