    "":"The color of 'light yellow' tea soup shows a soft yellow tone, is clear and transparent, and usually has a certain sense of lightness and peace. It is a relatively mild color of tea soup.",
    "":"'Golden' tea soup has a bright golden hue, bright and full color, extremely high transparency and gloss, and usually has a rich aroma and taste, which is a typical feature of high-end tea soup.",
    "":"The color of 'orange-yellow' tea soup shows obvious mixed characteristics of orange and yellow, usually showing a bright and full appearance, and the color is bright, but not as bright as golden yellow, but more inclined to orange.",
    "":"The color of 'orange red' tea soup shows obvious mixed characteristics of orange and red, usually showing a bright and rich appearance, bright color and high gloss. It is a kind of tea soup color with special flavor.",
    "":"'Red soup' tea soup has an obvious red hue, bright and rich color, strong red color, rich aroma and mellow taste. It is a unique color of black tea and a symbol of its unique flavor.",
    "":"'Curd' display a milky or creamy state with low transparency and a cloudy texture, emitting a milky or creamy aroma. It's a unique feature of high-quality white teas or Pu-erh teas, symbolizing their distinct flavor profile.",
    "":"'Bright' tea soup shows a clear and transparent color, bright and bright color, high transparency, without any signs of turbidity or turbidity, which is one of the typical characteristics of high-quality tea soup and a symbol of its unique flavor.",
    "":"The color of 'turbid' tea soup is unclear and opaque, with a certain degree of turbidity. It is usually caused by tea leaf debris, fine particles or tea soup sediments suspended in the tea soup, which affects the transparency and taste of the tea soup. It is usually not of high quality. Characteristics of tea soup.",
    "":"'Dim' manifests as a dull, lackluster state with a dark, uninspiring color, lacking shine and vitality. It represents an undesirable condition for tea infusion, typically not characteristic of high-quality tea.",
